
#app {
	font-family: consolas, Menlo; 
	max-width: 700px; 
	margin: 0 auto;
}
.tabs, footer {
	list-style: none;
	padding: 5px 18px;  
	font-size: 24px; 
	background-color: #555;
	color: #EEE;
}
.tabs {
	margin-bottom: 1em;
}
.tabs a {
	color: inherit;
	text-decoration: none;
}
.tab-top {
	display: inline-block; 
	margin-right: 1em;
}
.outside {
	margin: 0 18px;
}
.para {
	margin-bottom: 1em;
}
.title {
	margin-top: 1em; 
	margin-bottom: .2em;
}
